:root{
    --clr-highlight: hsl(212, 100%, 50%);
    --clr-main-bg: hsl(227, 100%, 98%);
    --clr-card-bg: hsl(0, 0%, 100%);
    --clr-text: hsl(217, 35%, 45%);
    --clr-text-secondary: hsl(217, 20%, 51%);
    --clr-heading: hsl(217, 21%, 21%);
    --clr-unavailable: hsl(216, 28%, 72%);
    --clr-toggle-hover: black;
}

.form_container{
width: 100%;
max-width: 1000px;
margin: auto;
padding-left: 20px;
padding-right: 20px;
}

.form_div{
    margin-top: 30px;
    display: flex;
    align-items: center;
    background-color: var(--clr-card-bg);
    border-radius: 12px;
}

.form_div input{
    width: 100%;
    height: 60px;
    background-color: var(--clr-card-bg);
    border: none;
    padding-left: 50px;
    color: var(--clr-text);
    border-radius: 12px;
    font-family: 'Space Mono', monospace;
    font-size: 16px;
}
.form_div input:focus{
    background-color: var(--clr-card-bg);
    outline: none;
    color: var(--clr-heading);
}
.form_div input::placeholder{
    color: var(--clr-heading);
}

.form_div input::-ms-clear{
    display: none; 
    width : 0;
    height: 0;
}
.form_div input::-ms-reveal{
    display: none; 
    width : 0;
    height: 0;
}
.form_div input::-webkit-search-decoration,
.form_div input::-webkit-search-cancel-button,
.form_div input::-webkit-search-results-button,
.form_div input::-webkit-search-results-decoration{
    display: none; 
}



.form_div i{
    position: relative;
    width: 0%;
    left: 10px;
    font-size: 25px;
    color: var(--clr-highlight);
}

.form_div button{
    position: relative;
    
    padding: 12px 20px;
    left: -10px;
    border-radius: 12px;
    background-color: var(--clr-highlight);
    color: var(--clr-card-bg);
    border: none;
    font-family: 'Space Mono', monospace;
    font-size: 16px;
}

